.sidebar-hidden {
  transition: transform 0.4s ease-in;
  transform: translateX(-100%);
  position: absolute;
}

.sidebar-show {
  transition: transform 0.4s ease-in;
  transform: translateX(0%);
}

.toolbar-transition {
  transition: width 0.4s ease-in;
}

.textarea-transition {
  transition: width 0.4s ease-in;
}
